<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>教师管理</el-breadcrumb-item>
      <el-breadcrumb-item>教师列表</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!-- 搜索 -->
      <el-row :gutter="20">
        <el-col>
          <el-form ref="formRef" :model="queryVo" label-width="80px" inline>
            <el-form-item label="学生姓名" prop="stuName">
              <el-input
                v-model="queryVo.stuName"
                @keyup.enter.native="queryPage"
                placeholder="请输入学生姓名"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item label="荣誉类别" prop="stuhType">
              <el-input
                v-model="queryVo.stuhType"
                @keyup.enter.native="queryPage"
                placeholder="请输入荣誉类别"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item label="荣誉级别" prop="stuhLevel">
              <el-input
                v-model="queryVo.stuhLevel"
                @keyup.enter.native="queryPage"
                placeholder="请输入荣誉级别"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item label="获取时间" prop="stuhGetTime">
              <el-input
                v-model="queryVo.stuhGetTime"
                @keyup.enter.native="queryPage"
                placeholder="请输入获取时间"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" icon="el-icon-search" @click="queryPage"
                >搜索</el-button
              >
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>

      <el-row :gutter="10" class="el-row">
        <el-col :span="1.5">
          <el-button
            type="primary"
            icon="el-icon-plus"
              v-if="is_authority.is_user_add"
            size="mini"
            @click="open = true"
            >新增</el-button
          >
          
        </el-col>
        
        <el-col :span="1.5">
          <el-button
            type="success"
            icon="el-icon-edit"
            size="mini"
            :disabled="single"
            @click="editUser"
            v-if="is_authority.is_user_upt"
            >修改</el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            :disabled="multiple"
            v-if="is_authority.is_user_del"
            @click="del"
            >删除</el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button type="info" icon="el-icon-upload2" size="mini"
            >导入</el-button
          >
          <!-- @click="handleImport" -->
          <!-- v-hasPermi="['system:user:import']" -->
        </el-col>
        <el-col :span="1.5">
          <el-button type="warning" icon="el-icon-download" size="mini"
            >导出</el-button
          >
          <!-- @click="handleExport"
                      v-hasPermi="['system:user:export']" -->
        </el-col>
        <!-- <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar> -->
      </el-row>
      <el-dialog
          :visible.sync="dialogVisible">
          
          <img width="100%" :src="photoView" >

        </el-dialog>
      <!-- 数据 -->
      <el-table
        :data="List"
        border
        stripe
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="50" align="center" />

        <el-table-column
          type="index"
          width="80"
          align="center"
          label="用户编号"
        ></el-table-column>

        <el-table-column
          prop="stuName"
          align="center"
          label="学生名称"
        ></el-table-column>

        <el-table-column
          prop="stuhType"
          label="荣誉类别"
          align="center"
        ></el-table-column>

        <el-table-column
          prop="stuhLevel"
          label="荣誉级别"
          align="center"
        ></el-table-column>

        <el-table-column
          prop="stuhGetTime"
          label="获取时间"
          align="center"
        ></el-table-column>

        <el-table-column prop="stuhPhoto" label="证书照片" align="center">
          <template slot-scope="scope">
              <img
                @click="photo(scope.row)"
                :src="scope.row.stuhPhoto"
                style="width: 50px;height: 50px"
              >
          </template>
        </el-table-column>

        


        <el-table-column
          prop="stuhDemo"
          label="简介"
          align="center"
          show-overflow-tooltip
        ></el-table-column>

        <el-table-column label="操作" width="120px" align="center">
          <template slot-scope="scope">
            <!-- 修改 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="编辑"
              v-if="is_authority.is_user_upt"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editUser(scope.row)"
              ></el-button>
            </el-tooltip>
            <!-- 删除 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="删除"  v-if="is_authority.is_user_del"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="del(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page.current"
        :page-sizes="[5, 10, 20, 30, 40]"
        :page-size="page.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
      ></el-pagination>
      <!-- 对话框 :before-close="handleClose" 在对话框关闭之前执行-->
      <!-- 标题 -->
      <el-dialog
        title="编辑"
        :visible.sync="open"
        width="50%"
        @close="closeInit"
      >
        <!-- 内容主体 -->

        <el-form
          :model="Form"
          :rules="rules"
          ref="formRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="学生" prop="stuId">
                <el-select
                  v-model="Form.stuId"
                  filterable
                  placeholder="请输入内容"
                >
                  <el-option
                    v-for="item in stuOptions"
                    :key="item.stuId"
                    :label="item.stuName"
                    :value="item.stuId"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="荣誉类别" prop="stuhType">
                <el-input v-model="Form.stuhType"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="证书级别" prop="stuhLevel">
                <el-input v-model="Form.stuhLevel"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="获取时间" prop="stuhGetTime">
                <el-input v-model="Form.stuhGetTime"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item
                label-width="100px"
                label="证书照片"
                prop="stuhPhoto"
              >
                <el-upload
                  :file-list="fileList"
                  class="upload-demo"
                  action="http://www.dayanhulian.com:8001/matter/fileoss/uploadOssFile"
                  multiple
                  :headers="config"
                  :on-success="saveUrl"
                  :before-upload="beforeUpload"
                  accept=".jsp,.png"
                >
                  <el-button icon="el-icon-upload2" size="small" type="primary"
                    >点击上传</el-button
                  >
                  <span slot="tip" class="el-uploaddTip">只能上传jpg/png文件</span>
                </el-upload>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="简介" prop="stuhDemo">
                <el-input v-model="Form.stuhDemo"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <!-- 按钮 -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="open = false">取 消</el-button>
          <el-button type="primary" @click="submitForm">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      photoView:'',
      dialogVisible: false,
      fileList: [],
      stuOptions: [],
      Form: {},
      ids: [],
      open: false,
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      List: [],
      queryVo: {
        stuName: undefined,
        page: {
          current: 1,
          size: 5
        }
      },
      page: {
        current: 1,
        size: 5,
        total: 0
      },
      token: window.sessionStorage.getItem('token'),
      rules: {
        licInstitution: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        stuhPhoto: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        stuId: [{ required: true, message: '该项不能为空', trigger: 'blur' }],
        stuhType: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        licType: [{ required: true, message: '该项不能为空', trigger: 'blur' }],
        licNo: [{ required: true, message: '该项不能为空', trigger: 'blur' }],
        stuhGetTime: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        stuhDemo: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        stuhLevel: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        licPeriodLimit: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    ...mapState(['btus']),
    config() {
      return { Token: this.token }
    }
  },
  created() {
    this.isAuthority()
    this.queryPage()
    this.getTreeselect()
  },
  methods: {
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    photo(row){
      this.dialogVisible = true
      this.photoView=row.stuhPhoto
    },
    beforeUpload(file){
        console.log(file.type);

        const isjpg = file.type === 'image/png' ||file.type === 'image/jpeg';
        if (!isjpg) {
          this.$message.error('请上传 JPG或者PNG 格式!');
        }
        return isjpg
    },
    async saveUrl(response, file, fileList) {
      this.Form.stuhPhoto = response.data.url
    },
    async getTreeselect() {
      const { data: res } = await this.$http.post(
        '/studentInfo/queryStudentInfoList',
        {
          page: {
            current: 1,
            size: 999
          }
        }
      )
      this.stuOptions = res.data.dataList.records
      if (res.code != 20000) return this.$message.error(res.message)
      //   console.log(this.stuOptions)
      // console.log(this.majorOptions)
    },

    unique(arr) {
      const res = new Map()
      return arr.filter(arr => !res.has(arr.id) && res.set(arr.id, 1))
    },
    submitForm() {
      this.$refs.formRef.validate(async valid => {
        if (valid) {
      console.log(this.Form);

          const { data: res } = await this.$http.post(
            '/stuHonorInfo/saveOrUpdateStuHonorInfo',
            this.Form
          )
          if (res.code != 20000) return this.$message.error(res.message)
          this.open = false
          this.queryPage()
          return this.$message.success(res.message)
        }
      })
    },

    closeInit() {
      // console.log(this.Form);
      this.Form = {}
      this.$refs.formRef.resetFields()
    },

    async editUser(row) {
      this.open = true
      const id = row.id || this.ids
      console.log(id)
      const { data: res } = await this.$http.get(
        '/stuHonorInfo/getByIdStuHonorInfo/' + id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      console.log(res)
      this.Form = res.data.entity
    },

    del(row) {
      if (row.id) {
        var a = row.id.split(',')
      }
      console.log(row)
      const userid = a || this.ids
      this.$confirm('确认要删除吗?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const { data: res } = await this.$http.delete(
            '/stuHonorInfo/removeByIdStuHonorInfos',
            {
              data: userid
            }
          )
          // console.log(res);
          this.queryPage()
          this.List = []
        })
        .then(() => {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
        })
    },
    async queryPage() {
      const { data: res } = await this.$http.post(
        '/stuHonorInfo/queryStuHonorInfoList',
        this.queryVo
      )
      console.log(res.data.dataList.records)
      if (res.code != 20000) return this.$message.error(res.message)
      this.List = res.data.dataList.records
      this.page.total = res.data.dataList.total
      this.page.current = res.data.dataList.current
      this.page.size = res.data.dataList.size
      // console.log(this.List)
    },
    //选择框
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length != 1
      this.multiple = !selection.length
    },
    // 监听每页展示的条数
    handleSizeChange(newSize) {
      this.queryVo.page.size = newSize
      this.queryPage()
    },
    //监听页码
    handleCurrentChange(newCurrent) {
      this.queryVo.page.current = newCurrent
      this.queryPage()
    },
    closeInit() {
      // console.log(this.Form);
      this.Form = {}
      this.$refs.formRef.resetFields()
    }
  }
}
</script>
<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  font-size: 12px;
}
.el-row {
  display: flex;
  align-items: center;
}
.el-pagination {
  margin-top: 15px;
}
.el-uploaddTip{
  margin-left: 10px;
}
</style>
